body {
  padding-top: 66px;
  background: #f1f1f1;
  font-family: 'Roboto', sans-serif;
  overflow-x: hidden; }

@media (min-width: 768px) {
  .body-width {
    margin-left: 6%;
    margin-right: 6%; } }

.site-container {
  overflow-x: hidden; }

h1 {
  font-size: 26px;
  font-weight: 500; }

h2 {
  font-size: 20px;
  font-weight: 400; }

h3 {
  font-size: 20px;
  font-weight: 300; }

h4 {
  font-size: 18px;
  font-weight: 500; }

h5 {
  font-size: 17px;
  font-weight: 700; }

h6 {
  font-size: 16px;
  font-weight: 500; }

.dropdown-menu {
  border-radius: 0;
  border: none;
  padding: 0; }
  .dropdown-menu li a {
    padding: 10px 20px;
    font-weight: 400;
    font-size: 15px;
    color: #999; }
  .dropdown-menu li a:hover, .dropdown-menu li a.focus {
    background-color: #373b44;
    color: white; }
  .dropdown-menu .divider {
    margin: 0;
    height: 1px;
    background: #eee; }

ul.pagination li a {
  color: black;
  padding: 10px 20px;
  border: 2px solid white;
  background: white;
  font-size: 20px;
  font-weight: 200;
  box-shadow: 0 4px 5px #ccc;
  border-radius: 5px !important;
  margin-bottom: 5px; }

ul.pagination li a:hover {
  background-color: #2196F3;
  color: white;
  border: 2px solid #2196F3; }

ul.pagination li.active span, ul.pagination li.active span:hover {
  padding: 10px 20px;
  border: 2px solid #2196F3;
  font-size: 20px;
  background-color: #2196F3;
  color: white;
  font-weight: 200;
  border-radius: 5px !important;
  box-shadow: 0 4px 5px #ccc;
  margin-bottom: 5px; }

ul.pagination li.disabled span, ul.pagination li.disabled span:hover {
  background-color: #f7f7f7;
  padding: 10px 20px;
  border: 2px solid #ddd;
  font-size: 20px;
  background-color: #ddd;
  color: #ccc;
  border-radius: 5px !important;
  margin-bottom: 5px; }

ul.pagination span, ul.pagination a {
  margin-right: 10px;
  border-radius: 0 !important;
